.point-modal {
    width: 1212px;
    height: 100%;
    max-height: 822px;
    background-color: $white;
    border-radius: 8px;
    position: relative;
    text-align: left;

    &__close {
        position: absolute;
        top: 24px;
        right: 24px;
        border: 0;
        padding: 0;
        cursor: pointer;
        outline: none;
        width: 24px;
        height: 24px;
        background-image: url("../img/menu-close-icon.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-color: transparent;
    }

    &__title {
        @include title-h2;

        padding: 64px 0 40px 42px;
        border-bottom: 1px solid $button-second;
    }

    &__overlay {
        position: fixed;
        z-index: 1000;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(33, 37, 41, 0.8);
        display: flex;
        align-items: center;
        justify-content: center;

        &--hide {
            display: none;
        }
    }

    &__info {
        display: flex;
        justify-content: flex-start;
        position: relative;
        height: calc(100% - 143px);
    }

    &__gallery {
        width: 852px;
        border-right: 1px solid $button-second;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    &__no-gallery {
        width: 852px;
        border-right: 1px solid $button-second;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        .point-modal__gallery-top {
            height: calc(100% - 117px);
            width: 852px;
            background-color: $background-select;
            border-bottom: 1px solid $button-second;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            text-align: center;

            svg {
                width: 472px;
                height: 56px;
                fill: $button-second;
            }

            p {
                color: $button-second;
                font-size: 0.875rem;
                width: 290px;
                margin: 36px 0 0;
            }
        }

        .point-modal__gallery-thumbs {
            height: 117px;
            width: 128px;

            .point-modal__gallery-slide-img {
                height: 85px;
                width: auto;
            }
        }

        &--hide {
            display: none;
        }
    }

    &__gallery-wrapper {
        display: flex;
        box-sizing: content-box;

        &--top {
            height: 100%;
            width: 100%;
            margin: 0 auto;
        }

        &--thumbs {
            height: 100%;
            width: 100%;
        }
    }

    &__gallery-top {
        height: calc(100% - 117px);
        width: 100%;
        overflow: auto;
        scrollbar-width: none;
        -ms-overflow-style: none;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__gallery-slide-top {
        height: 100%;
        width: auto;
        position: relative;

        .point-modal__gallery-slide-img {
            height: 100%;
            width: auto;
            object-fit: cover;
        }
    }

    &__gallery-thumbs {
        max-height: 85px;
        width: 422px;
        margin: 16px auto;
        overflow: auto;
        scrollbar-width: none;
        -ms-overflow-style: none;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__gallery-slide-thumbs {
        height: 100%;
        width: auto;
        margin: 0 4px 0 0;

        .point-modal__gallery-slide-img {
            height: 85px;
            width: auto;
        }
    }

    &__gallery-prev,
    &__gallery-next {
        position: absolute;
        bottom: 35px;
    }

    &__gallery-prev {
        left: 150px;
        cursor: pointer;
        width: 48px;
        height: 48px;
        background-image: url("../img/modal-arrow-left-blue.svg");
        background-size: cover;

        &:hover {
            background-image: url("../img/modal-arrow-left_hover.svg");
        }
    }

    &__gallery-next {
        right: 150px;
        cursor: pointer;
        width: 48px;
        height: 48px;
        background-image: url("../img/modal-arrow-right-blue.svg");
        background-size: cover;

        &:hover {
            background-image: url("../img/modal-arrow-right_hover.svg");
        }
    }

    &__description {
        box-sizing: border-box;
        padding: 24px 24px 32px;
        overflow-y: auto;
        overflow-x: hidden;

        &::-webkit-scrollbar-track {
            border-radius: 4px;
        }

        &::-webkit-scrollbar {
            width: 4px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 4px;
            background: $button-second;
        }
    }

    &__point-address {
        font-size: 0.875em;
        margin: 0;
    }

    &__point-title {
        font-family: $Stratos;
        margin: 8px 0 0;
        font-size: 1em;
    }

    &__point-about {
        margin: 20px 0 0;
    }

    &__point-metro {
        font-size: 0.875rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 8px 0;

        &::before {
            margin: 0 8px 0 0;
            display: block;
            content: "";
            width: 16px;
            height: 16px;
            background-image: url("../img/metro-icon.svg");
        }
    }

    &__point-services {
        @include list;
    }

    &__point-service {
        font-size: 14px;
        line-height: 140%;
        width: 263px;
        margin: 0 0 16px;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &::before {
            display: block;
            content: "";
            min-width: 32px;
            height: 32px;
            background-repeat: no-repeat;
            background-size: cover;
            margin: 0 10px 0 0;
        }

        &--epoms {
            &::before {
                background-image: url("../img/service-oms.svg");
            }
        }

        &--card {
            &::before {
                background-image: url("../img/service-registration.svg");
            }
        }

        &--full_day {
            &::before {
                background-image: url("../img/service-long.svg");
            }
        }

        &--weekend {
            &::before {
                background-image: url("../img/service-weekend.svg");
            }
        }

        &--save_policy {
            &::before {
                background-image: url("../img/service-e-policy.svg");
            }
        }

        &--policy {
            &::before {
                background-image: url("../img/service-policy.svg");
            }
        }

        &--priem {
            &::before {
                background-image: url("../img/service-manager.svg");
            }
        }

        &--ecard {
            &::before {
                background-image: url("../img/service-card.svg");
            }
        }

        &--invalid {
            &::before {
                background-image: url("../img/service-disabled.svg");
            }
        }
    }
}

@media (max-width: $tablet-max-width) {
    .point-modal {
        width: 100vw;
        height: 100vh;
        max-height: unset;
        border-radius: unset;


        &__close {
            top: 32px;
            right: 32px;
        }

        &__title {
            font-size: 1.28em;
            padding: 32px 0 32px 32px;
        }

        &__info {
            flex-direction: column;
            height: calc(100vh - 86px);
        }

        &__gallery {
            width: 100%;
            max-width: $tablet-max-width;
            border-right: none;
        }

        &__gallery-wrapper {
            &--top {
                height: 423px;
            }

            &--thumbs {
                height: 100%;
                width: 100%;
                //margin: 16px auto;
            }
        }

        &__no-gallery {
            display: none;
        }

        &__gallery-top {
            height: 423px;
            overflow-y: hidden;
        }

        &__gallery-slide-top {
            height: 423px;

            .point-modal__gallery-slide-img {
                height: 423px;
            }
        }

        &__gallery-thumbs {
            margin: 24px auto 40px;
            overflow-y: hidden;
        }

        &__gallery-prev,
        &__gallery-next {
            bottom: 50px;
        }

        &__gallery-prev {
            left: calc(50% - 288px);
        }

        &__gallery-next {
            right: calc(50% - 288px);
        }

        &__description {
            padding: 0 32px 40px;
        }

        &__point-address {
            font-size: 1em;
        }

        &__point-title {
            font-size: 1.28em;
        }

        &__point-about {
            margin: 20px 0 0;
            font-size: 14px;

            p,
            a {
                font-size: 0.75rem;
            }
        }
    }
}

@media (max-width: $mobile-max-width) {
    .point-modal {
        &__close {
            top: 24px;
            right: 24px;
        }

        &__title {
            padding: 40px 0;
            border: none;

            span {
                display: none;
            }
        }

        &__info {
            height: calc(100vh - 81px);
        }

        &__gallery {
            max-width: $mobile-max-width;
        }

        &__gallery-wrapper {
            &--top {
                height: 423px;
            }

            &--thumbs {
                height: 100%;
                width: 100%;
                //margin: 16px auto;
            }
        }

        &__gallery-top {
            height: 250px;
            margin-left: 24px;
        }

        &__gallery-slide-top {
            height: 250px;
            margin: 0 8px 0 0;

            .point-modal__gallery-slide-img {
                height: 250px;
            }
        }

        &__gallery-thumbs {
            display: none;
        }

        &__description {
            padding: 24px 24px 40px;
        }

        &__point-address {
            font-size: 1em;
        }

        &__point-title {
            font-size: 1.28em;
        }

        &__point-about {
            margin: 20px 0 0;
            font-size: 14px;

            p,
            a {
                font-size: 0.75rem;
            }
        }

        &__point-service {
            font-size: 12px;
            width: 100%;
            max-width: 263px;
        }
    }
}
