      display: unset;
      text-align: left;

      .tab-item__point-modal {
        margin: 24px 0 0;
        height: unset;
        background-color: unset;
        border-radius: unset;

        h2 {
          display: none;
        }
      }

      .tab-item__point-modal-info {
        border: none;
        justify-content: flex-start;
        height: unset;
      }

      .tab-item__point-list {
        position: relative;
        height: max-content;
        max-height: max-content;
        width: 100%;
        display: flex;
      }

      .tab-item__point-address {
        margin-bottom: 8px;
      }

      .tab-item__point-item {
        max-width: 1215px;
        box-sizing: border-box;
        width: 100%;
        min-width: unset;
        border-top: 1px solid $button-second;
        border-left: none;
        border-right: none;
        border-bottom: 1px solid $button-second;

        &:nth-child(2n) {
          margin-left: 0;
        }

        &:nth-child(n+2) {
          margin-top: -1px;
        }

        &:not(:first-child) {
          border-top: 1px solid $button-second;
          display: block;
        }
      }

      .tab-item__point-info {
        input ~ .tab-item__point-about {
          display: block;
        }
      }

      .tab__info-list {
        &--point {
          margin: 0 0 16px;

          .tab__info-description {
            letter-spacing: unset;
            width: 100%;
            margin: 0;
          }

          .tab-item__point-services {
            display: flex;
            justify-content: flex-start;
          }

          .tab-item__point-service {
            width: 24px;
            margin: 0 8px 0 0;

            &::before {
              min-width: 24px;
              height: 24px;
              margin: 0;
            }

            span {
              display: none;
            }
          }
        }
      }

      .tab__info-item {
        input,
        label {
          display: none;
        }

        &:last-child {
          display: none;
        }

        input:checked + label {
          display: none;
        }

        input ~ .tab__info-description {
          display: block;
        }
      }

      .tab-item__point-info-button {
        display: none;
      }

      .tab-item__point-today {
        display: block;
        margin: 0;

        span {
          color: $blue;
        }
      }

      .tab-item__point-day,
      .tab-item__point-time,
      .tab-item__point-break,
      .tab-item__point-weekend,
      .tab-item__point-holidays {
        display: none;
      }
    }

    &--address-modal {
      background: $white;

      .tab-item__point-gallery {
        height: 250px;
        width: 100%;
      }

      .tab-item__point-gallery-top {
        height: 250px;
      }

      .tab-item__point-gallery-thumbs {
        display: none;
      }
    }

    &--health-address {
      .tab-item__point-modal {
        height: 330px;
        max-width: $mobile-max-width;
      }

      .tab-item__point-modal-info {
        height: 330px;

        &::before {
          display: none;
        }
      }

      .tab-item__point-map {
        height: 330px;
      }

      .tab-item__point-list {
        display: none;
      }
    }
  }

  .tab-item__point-modal {
    h2 {
      padding: 24px 105px 24px 24px;
    }
  }

  .tab-item__point-modal-close {
    top: 24px;
    right: 24px;
  }

  .tab-item__point-modal-info {
    height: calc(100vh - 240px);
  }

  .tab-item__point-list {
    max-width: $mobile-max-width;
  }

  .tab-item__point-item {
    max-width: $mobile-max-width;
    padding: 24px;
  }

  .tab-item__point-map-pin {
    width: 18px;
    height: 25px;

    button {
      width: 18px;
      height: 25px;

      svg {
        width: 18px;
        height: 25px;
      }
    }
  }

  .tab-item__view-button + label {
    display: inline-block;
    width: 41%;

    &:not(:last-of-type) {
      margin: 0 12px 0 24px;
    }
  }

  .tab-item__view-button--map:checked ~ .tab-item__point-overlay--address,
  .tab-item__view-button--list:checked ~ .tab-item__point-overlay--list {
    display: block;
  }

  .tab-item__point-overlay--address,
  .tab-item__point-overlay--list {
    display: none;
  }
}
