html {
	height: 100%;
}

body {
	width: 100%;
	min-width: $desktop-min-width;
	background-color: $white;
	margin: 0 auto;
	text-align: center;
	font-family: "Arial", "Helvetica", sans-serif;
	font-size: 16px;
	line-height: 150%;
	font-style: normal;
	color: $black-text;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-x: hidden;
	position: relative;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
	white-space: nowrap;
	clip-path: inset(100%);
}

.up-button {
	position: fixed;
	right: 100px;
	bottom: 30px;
	text-align: center;
	cursor: pointer;
	z-index: 1000;

	&:hover {
		a {
			position: relative;
			color: $hover-text;
			background-image: url("../img/arrow_up-hover-icon.svg");
		}

		&::before {
			position: absolute;
			top: 0;
			left: 6px;
			display: block;
			content: "";
			width: 48px;
			height: 48px;
			border-radius: 50%;
			box-shadow: 0 10px 16px rgba(0, 0, 120, 0.1);
		}
	}

	a {
		text-decoration: none;
		margin: 0;
		font-size: 14px;
		line-height: 17px;
		font-weight: bold;
		letter-spacing: 0.03em;
		text-transform: uppercase;
		color: $light-gray;
		padding-top: 60px;
		display: block;
		width: 60px;
		height: 77px;
		background-image: url("../img/arrow_up-icon.svg");
		background-repeat: no-repeat;
		background-size: 48px 48px;
		background-position: top center;
	}
}

.up-button-hide {
	display: none;
}

.hide {
	display: none;
}

.networks {
	position: fixed;
	z-index: 100;
	right: 6px;
	top: 180px;
}

.networks__list {
	list-style: none;
	margin: 0;
	padding: 0;
}

.networks__item {
	width: 36px;
	margin: 0 0 5px;
}

.networks__item-link {
	svg {
		width: 36px;
		height: 36px;
		fill: $blue;
	}

	svg:hover {
		fill: $hover-text;
	}
}

.no-scroll {
	overflow: hidden;
}

.page-wrapper {
	width: 100vw;
}

a,
input {
	outline: none;
}

@media (max-width: $tablet-max-width) {
	body {
		min-width: $tablet-min-width;
		font-size: 14px;
		line-height: 160%;
	}

	.up-button {
		right: 20px;
	}
}

@media (max-width: $mobile-max-width) {
	body {
		min-width: $mobile-min-width;
	}

	.up-button {
		display: none;
	}

	.networks {
		display: none;
	}
}
