.input {
    position: relative;

    &--select {
        &::before {
            pointer-events: none;
            font-family: $icon;
            color: $blue;
            font-size: 16px;
            content: '\e900';
            position: absolute;
            right: 16px;
            top: 17px;
        }
    }

    &--date {
        &::before {
            font-family: $icon;
            font-size: 24px;
            color: $gray;
            content: '\e903';
            position: absolute;
            right: 16px;
            top: 16px;
        }
    }

    &--icon {
        input {
            padding-right: 40px;
        }

        &::after {
            font-family: $icon;
            font-size: 24px;
            line-height: 24px;
            position: absolute;
            right: 16px;
            top: 50%;
            transform: translateY(-50%);
            color: $blue;
        }

        &-search {
            &::after {
                content: '\e902';
            }
        }
    }

    &__label {
        position: absolute;
        pointer-events: none;
        top: 18px;
        left: 0;
        color: $light-gray;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 100%;
        box-sizing: border-box;
        padding: 0 16px;
        transition: 0.3s ease;
        transition-property: transform, top, font-size;

        &--select {
            top: 9px;
            transform: translateY(0);
            font-size: 12px;
        }
    }

    &__value {
        font-size: 14px;
        line-height: 20px;
        height: 56px;
        padding: 26px 16px 10px;
        box-sizing: border-box;
        border: 1px solid $button-second;
        border-radius: 8px;
        background-color: $white;
        text-overflow: ellipsis;
        transition: 0.3s ease;
        transition-property: border-color, box-shadow;

        &[type='date'] {
            -webkit-appearance: none;

            &::-webkit-inner-spin-button {
                display: none;
                -webkit-appearance: none;
            }

            &::-webkit-calendar-picker-indicator {
                cursor: pointer;
                position: absolute;
                right: 0;
                width: 56px;
                height: 56px;
                top: 0;
                color: transparent;
                opacity: 0;
            }
        }

        &--select {
            width: 100%;
            -webkit-appearance: none;
            appearance: none;
            padding-left: 15px;

            &::-ms-expand {
                display: none;
            }
        }

        &--centered {
            padding: 0 16px;
        }
    }

    &__error {
        padding-top: 4px;
        font-size: 14px;
        line-height: 20px;
        color: $orange;
        display: none;
    }

    &__difficulty {
        padding-top: 4px;

        &-label {
            transition: 0.3s ease;
            transition-property: color;
            font-size: 12px;
            line-height: 14px;

            &--red {
                color: $red;
            }

            &--yellow {
                color: $yellow;
            }

            &--green {
                color: $green;
            }
        }
    }

    &__eye {
        position: absolute;
        top: 0;
        right: 0;
        height: 56px;
        width: 56px;
        text-align: center;

        &::before {
            font-family: $icon;
            content: '\e906';
            font-size: 24px;
            line-height: 56px;
            color: $gray;
            transition: 0.3s ease;
            transition-property: color;
        }

        &.active {
            &::before {
                color: $blue;
            }
        }
    }

    &__line {
        position: absolute;
        height: 4px;
        left: 1px;
        right: 1px;
        top: 51px;
        border-radius: 0 0 4px 4px;
        overflow: hidden;
        transition: 0.7s ease;
        transition-property: background-color;

        div {
            height: 100%;
            transition: 0.7s ease;
            transition-property: width, background-color;
        }

        &--red {
            background-color: $redLight;

            div {
                background-color: $red;
                width: 25%;
            }
        }

        &--yellow {
            background-color: $yellowLight;

            div {
                background-color: $yellow;
                width: 50%;
            }
        }

        &--green {
            background-color: $greenLight;

            div {
                background-color: $green;
                width: 90%;
            }
        }
    }

    &__tooltip {
        position: absolute;
        top: 24px;
        left: 100%;
        transform: translateX(4px);
        border-radius: 50%;
        box-sizing: border-box;
        border: 1px solid $gray;
        width: 16px;
        height: 16px;
        font-size: 8px;
        line-height: 16px;
        text-align: center;
        color: $light-gray;
        background-color: $background-select;
    }

    &.focus {
        .input {
            &__value {
                &:focus {
                    border-color: $light-gray;
                    box-shadow: 0 5px 10px rgba(0, 0, 120, 0.05);
                }
            }

            &__label {
                top: 9px;
                transform: translateY(0);
                font-size: 12px;
            }
        }
    }

    &.error {
        .input {
            &__value {
                border-color: $red;
            }

            &__error {
                display: block;
            }
        }
    }

    @media (min-width: $tablet) {
        &__tooltip {
            top: 16px;
            transform: translateX(8px);
            width: 28px;
            height: 28px;
            font-size: 14px;
            line-height: 28px;
        }
    }

    @media (min-width: $desktop) {
        &__value {
            &:hover {
                border-color: $light-gray;
                box-shadow: 0 5px 10px rgba(0, 0, 120, 0.05);
            }
        }

        &__tooltip {
            transform: translateX(12px);
        }
    }
}
