.tab-item {
    width: 800px;
    text-align: left;

    h1 {
        @include title-h1;
    }

    h2 {
        @include title-h2;
    }

    h3 {
        @include title-h3;

        line-height: 34px;
        color: $blue;
        margin: 40px 0 32px;
    }

    & > h4 {
        @include title-h4;

        color: $blue;
    }

    &--address {
        width: 1212px;

        h1 {
            width: 796px;
        }
    }

    &--health {
        width: 800px;

        .tab-item__point-map {
            height: 640px;
            position: relative;
        }

        .tab-item__point-pin {
            position: absolute;
            top: 45%;
            left: 45%;
            cursor: pointer;
        }

        .tab-item__view-section--list {
            .tab-item__point-list {
                border: 1px solid $button-second;
            }

            .tab-item__point-item {
                width: 800px;
                border: none;
                border-bottom: 1px solid $button-second;
            }
        }
    }

    &--about-oms {
        & > h4 {
            margin: 40px 0 16px;
        }
    }

    &--sitemap {
        width: 100%;
    }

    &--test {
        position: relative;
        margin: 0 auto;
    }
}

.tab-item__about-description {
    @include text;

    margin: 0 0 16px;
    width: 697px;

    a {
        color: $blue;
        text-decoration: none;
    }

    & > ol {
        padding: 0 0 0 16px;

        li {
            margin: 0 0 16px;
        }
    }

    &--first {
        margin: 40px 0 32px;
    }
}

.tab-item__about-list {
    @include list;

    counter-reset: myCounter;

    & > li {
        margin: 0 0 32px;
    }

    li > h5 {
        @include title-h5;

        color: $blue;
        display: inline-block;
        margin: 0 0 16px;

        &::before {
            @include title-h5;

            color: $blue;
            margin: 0 5px 0 0;
            counter-increment: myCounter;
            content: counter(myCounter) '.';
            display: inline-block;
        }
    }

    .tab__document-list {
        margin: 24px 0 0;
    }

    &--simple {
        & > li {
            margin: 0 0 16px;

            ol {
                margin: 16px 0 0;
                list-style: none;
                padding: 0 0 0 38px;

                & > li {
                    margin: 0 0 16px;

                    &::before {
                        display: inline-block;
                        margin: 0 5px 0 0;
                    }

                    &:nth-child(1) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(2) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(3) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(4) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(5) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(6) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(7) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(8) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(9) {
                        &::before {
                            content: ')';
                        }
                    }

                    &:nth-child(10) {
                        &::before {
                            content: ')';
                        }
                    }
                }
            }

            &::before {
                @include text;

                margin: 0 5px 0 0;
                line-height: 150%;
                font-family: "Arial", "Helvetica", sans-serif;
                color: $black-text;
                counter-increment: myCounter;
                content: counter(myCounter) ')';
                display: inline-block;
            }
        }
    }
}

.tab-item__emphasis {
    @include title-h4;

    color: $blue;
    box-sizing: border-box;
    padding: 24px 24px 20px;
    background-color: $background-select;
    border-radius: 8px;
    margin: 0 0 32px;
}

.tab-item__list-container {
    width: 100%;
    min-width: 800px;
    max-width: 800px;
    height: max-content;
    max-height: 1212px;
    margin: 37px auto 0;
    overflow: hidden;

    &--article {
        margin: 56px auto 0;
        position: relative;
        height: 388px;
    }

    &--health {
        max-height: 2088px;

        .tab-item__list-slide {
            max-height: 504px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            box-sizing: border-box;
            padding: 32px 32px 40px;
        }

        .tab-item__list-slide-title {
            h4 {
                @include title-tablet-h2;

                margin: 0 0 16px;
            }
        }
    }
}

.tab-item__list-wrapper {
    @include list;

    width: 100%;
    min-width: 800px;
    max-width: 800px;
    height: max-content;
    display: flex;
    flex-flow: row wrap;

    &--article {
        height: 388px;
        flex-flow: row nowrap;
    }

    &--tests {
        .tab-item__list-slide {
            min-height: 344px;
            max-height: 344px;
            padding: 32px 32px 28px;
        }

        .tab-item__test-link {
            text-decoration: none;
        }

        .tab-item__list-slide-title {
            margin: 24px 0 0;

            h4 {
                @include title-h5;

                margin: 0;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;

                &:hover {
                    color: $hover-text;
                }
            }
        }
    }
}

.tab-item__list-slide {
    width: 100%;
    max-width: 386px;
    min-height: 386px;
    max-height: 386px;
    border: 1px solid $button-second;
    transition: box-shadow 0.5s;
    position: relative;
    z-index: 10;
    margin: 0 24px 24px 0;

    &::after {
        display: block;
        content: "";
        width: 53px;
        height: 100%;
        background-image: url("../img/pattern.svg");
        position: absolute;
        z-index: -10;
        top: 0;
        right: 0;
        opacity: 0;
        transition: opacity 0.5s;
    }

    &:hover {
        box-shadow: 0 24px 20px -10px rgba(0, 0, 120, 0.1);
        transition: box-shadow 0.5s;

        .tab__honor-item-title {
            time {
                color: $blue;
            }

            h4 {
                color: $blue;
            }
        }

        &::after {
            opacity: 1;
            transition: opacity 0.5s;
        }
    }

    &--article {
        margin: 0 24px 0 0;
    }

    &:nth-child(2n) {
        margin: 0 0 24px 0;
    }

    &--result {
        box-sizing: border-box;
        padding: 32px;
        max-height: max-content;

        h5 {
            @include title-h5;
            color: $blue;
        }
    }
}

.tab-item__list-slide-link {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 386px;
    padding: 32px;
    text-decoration: none;
}

.tab-item__list-slide-title {
    time {
        font-size: 0.75em;
        line-height: 117%;
        letter-spacing: 0.02em;
        color: $black-text;
    }

    h4 {
        @include title-h4;

        color: $black-text;
        line-height: 30px;
        margin: 16px 0 0;
    }
}

.tab-item__list-slide-photos-list {
    @include list;

    display: flex;
    justify-content: flex-start;
    overflow: hidden;
}

.tab-item__list-slide-photos-item {
    margin: 0 22px 0 0;

    &--more {
        min-width: 64px;
        height: 64px;
        border-radius: 8px;
        background-color: $background-footer;
        font-family: $Stratos;
        font-size: 1.25em;
        line-height: 150%;
        color: $black-text;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.tab-item__list-slide-photos-item-pic {
    width: 64px;
    height: 64px;

    &--hide {
        display: none;
    }
}

.tab-item__list-navigation {
    margin: 56px auto 0;
    display: flex;
    justify-content: space-between;
    width: 480px;

    &--hide {
        display: none;
    }

    &--article {
        position: absolute;
        top: 264px;
        left: -62px;
        width: 923px;
        margin: 0;
    }
}

.tab-item__list-prev,
.tab-item__list-next {
    width: 48px;
    height: 48px;
    background-repeat: no-repeat;
    background-size: 48px 48px;
    cursor: pointer;
}

.tab-item__list-prev {
    background-image: url("../img/arrow_left-icon.svg");

    &:hover {
        background-image: url("../img/arrow_left-hover-icon.svg");
    }
}

.tab-item__list-next {
    background-image: url("../img/arrow_right-icon.svg");

    &:hover {
        background-image: url("../img/arrow_right-hover-icon.svg");
    }
}

.tab-item__list-pagination {
    max-width: 304px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin: 0;

    &--article {
        display: none;
    }
}

.tab-item__list-pagination-bullet {
    min-width: 46px;
    height: 46px;
    margin: 0 16px 0 0;
    border: 1px solid $button-second;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $Stratos;
    font-weight: normal;
    font-size: 1em;
    line-height: 150%;
    color: $blue;
    text-decoration: none;

    &:hover {
        border: 1px solid $hover-text;
        background-color: $hover-text;
        color: $white;
    }

    &:nth-child(4n) {
        margin: 0;
    }

    &--active {
        border: 1px solid $blue;
        background-color: $blue;
        color: $white;
    }
}

.tab-item__slide-topic,
.tab-item__slide-data,
.tab-item__slide-status {
    font-size: 0.75em;
    line-height: 117%;
    letter-spacing: 0.02em;
    color: $light-gray;
    margin: 32px 0 8px;
}

.tab-item__slide-status {
    color: $hover-text;
}

.tab-item__slide-info {
    display: flex;
    justify-content: flex-start;

    .tab-item__slide-topic,
    .tab-item__slide-data {
        margin: 0 8px 0 0;
    }

    .tab-item__slide-status {
        margin: 0 0 0 auto;
    }
}

.tab-item__slide-description {
    height: 100%;
    max-height: 120px;
    overflow: hidden;
    margin: 0 0 13px;

    p {
        margin: 0;
    }
}

.tab-item__slide-more {
    font-weight: bold;
    font-size: 0.875em;
    line-height: 120%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    text-decoration: none;
    color: $blue;

    &:hover {
        color: $hover-text;
    }
}

.tab-item__questionnaire {
    h3 {
        margin: 40px 0 16px;
    }

    p {
        @include text;

        margin: 0 0 24px;
    }
}

.tab-item__questionnaire-form {
    display: flex;
    justify-content: space-between;
}

.tab-item__questionnaire-button {
    background-color: $button-second;
    color: $blue;
    width: 283px;

    &:hover {
        color: $white;
    }
}

.tab-item__video-list {
    @include list;

    display: flex;
    justify-content: left;
    flex-wrap: wrap;
}

.tab-item__video-item {
    width: 48.5%;
    box-sizing: border-box;
    border: 1px solid $button-second;
    padding: 32px;
    margin: 0 24px 24px 0;

    &:last-child {
        margin: 0;
    }

    &:nth-child(2n) {
        margin: 0 0 24px;
    }

    iframe {
        width: 100%;
        height: 182px;
    }
}

.tab-item__video-item-title {
    margin: 24px 0 0;

    h2 {
        @include title-tablet-h2;

        margin: 0 0 8px;
    }

    time {
        font-size: 0.75em;
        line-height: 117%;
        letter-spacing: 0.02em;
        color: $light-gray;
    }
}

.tab-item__about-prevention {
    margin: 80px 0 0;

    h4 {
        margin: 24px 0 0;
    }

    p {
        margin: 16px 0 0;
    }

    ul {
        @include list;

        margin: 16px 0 0;
    }

    li {
        display: flex;
        justify-content: flex-start;
        margin: 0 0 8px;

        &::before {
            display: block;
            content: "\2014";
            margin: 0 5px 0 0;
        }
    }

    &--hide {
        display: none;
    }
}

.tab-item__about-prevention-more {
    background-color: $hover-text;
    color: $white;
    margin: -8px 0 0;
    border-radius: 0 0 8px 8px;
    box-sizing: border-box;
    padding: 24px 40px 32px;

    &--hide {
        display: none;
    }
}

.tab-item__about-prevention-more-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
        font-family: $Stratos;
        font-size: 1.25em;
        line-height: 150%;
        margin: 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &::before {
            display: block;
            content: "";
            min-width: 56px;
            height: 56px;
            background-image: url("../img/pin-icon.svg");
            margin: 0 16px 0 0;
        }
    }
}

.tab-item__about-prevention-more-button {
    color: $hover-text;
    background-color: $white;
    width: 226px;
    height: 56px;
    margin: 0 0 0 10px;

    &:hover {
        color: $hover-text;
        background-color: $white;
        box-shadow: none;
    }
}

.tab-item__picture {
    margin: 40px 0 0;

    img {
        display: block;
        margin: 0 auto;
        width: 640px;
        height: 948px;
    }

    &--structure {
        margin: 40px 0;

        img {
            width: 728px;
            height: 552px;
        }
    }

    &--time {
        margin: 0;
    }
}

.tab-item__point-services {
    @include list;
    display: flex;
    justify-content: flex-start;
    margin: 16px 0 0;
}

.tab-item__point-service {
    margin: 0 8px 0 0;
    min-width: 24px;
    height: 24px;
    background-size: cover;

    &--epoms {
        background-image: url("../img/service-oms.svg");
    }

    &--card {
        background-image: url("../img/service-registration.svg");
    }

    &--full_day {
        background-image: url("../img/service-long.svg");
    }

    &--weekend {
        background-image: url("../img/service-weekend.svg");
    }

    &--save_policy {
        background-image: url("../img/service-e-policy.svg");
    }

    &--policy {
        background-image: url("../img/service-policy.svg");
    }

    &--priem {
        background-image: url("../img/service-manager.svg");
    }

    &--ecard {
        background-image: url("../img/service-card.svg");
    }

    &--invalid {
        background-image: url("../img/service-disabled.svg");
    }
}

@media (max-width: 1366px) {
    .tab-item__list-navigation {
        &--article {
            position: absolute;
            top: 0;
            left: unset;
            right: 0;
            width: 112px;
            margin: 0;
        }
    }
}

@media (max-width: $tablet-max-width) {
    .tab-item {
        width: 100%;
        max-width: $tablet-max-width;
        padding: 0 32px;
        margin: 40px 0 0;
        box-sizing: border-box;

        h1 {
            font-size: 1.7em;
            width: 100%;
            max-width: 600px;
        }

        h2 {
            font-size: 1.28em;
        }

        h3 {
            @include title-tablet-h2;

            margin: 32px 0;
        }

        & > h4 {
            @include title-tablet-h2;

            color: $blue;
        }

        &--honor {
            max-width: 640px;
        }

        &--about-oms {
            h3 {
                margin: 32px 0 16px;
            }

            & > h4 {
                margin: 32px 0 16px;
            }
        }

        &--health {
            width: 100%;
            max-width: $tablet-max-width;

            .tab-item__point-map {
                height: 535px;
            }

            .tab-item__view-section--list {
                .tab-item__point-list {
                    position: relative;
                    border: 1px solid $button-second;
                }

                .tab-item__point-item {
                    width: 100%;
                    max-width: 1212px;
                    border: none;
                    border-bottom: 1px solid $button-second;

                    &:not(:first-child) {
                        border-top: none;
                        border-bottom: 1px solid $button-second;
                        display: block;
                    }
                }
            }
        }
    }

    .tab-item__about-description {
        @include text-tablet;

        margin: 0 0 16px;
        width: 100%;
        max-width: 1215px;

        &--first {
            margin: 32px 0 16px;
        }
    }

    .tab-item__emphasis {
        @include title-tablet-h2;

        color: $blue;
        padding: 24px 81px 24px 24px;
    }

    .tab-item__about-list {
        .tab__document-list {
            margin: 16px 0 0;
        }

        &--simple {
            & > li {
                ol {
                    padding: 0 0 0 16px;

                    & > li {
                        &::before {
                            display: inline-block;
                            margin: 0 5px 0 0;
                        }
                    }
                }
            }

            & > li::before {
                @include text-tablet;

                margin: 0 5px 0 0;
            }
        }
    }

    .tab-item__list-container {
        max-width: 576px;
        height: max-content;
        margin: 0 auto;

        &--article {
            margin: 24px auto 0;
            position: relative;
            height: 388px;
        }

        &--health {
            max-height: 1845px;
            min-width: 576px;
            max-width: 1215px;
            margin: 20px 0 0;

            .tab-item__list-wrapper {
                flex-flow: row wrap;
                height: max-content;
                min-width: 576px;
                width: 100%;
                max-width: 1215px;
                margin: 0 auto;
            }

            .tab-item__list-slide {
                min-width: 276px;
                width: 47.5%;
                max-width: 47.5%;
                max-height: 441px;
                box-sizing: border-box;
                padding: 32px 32px 38px;

                &:nth-child(2n) {
                    margin: 0 0 24px 0;
                }
            }

            .tab-item__list-slide-title {
                h4 {
                    @include title-tablet-h2;

                    line-height: 22px;
                    margin: 0 0 16px;
                }
            }
        }
    }

    .tab-item__list-wrapper {
        margin: 24px 0 0;
        height: 1152px;
        max-width: 576px;

        &--article {
            margin: 0;
        }

        &--interview-result {
            width: 100%;
            min-width: unset;
            max-width: 1212px;
            justify-content: center;
            height: unset;
        }
    }

    .tab-item__list-slide {
        max-width: 274px;
        max-height: 358px;

        &--result {
            max-width: 47%;
            flex-basis: 47%;
            box-sizing: border-box;
            padding: 32px;
            max-height: max-content;

            h5 {
                @include title-h5;

                color: $blue;
            }
        }
    }

    .tab-item__list-slide-link {
        padding: 31px 27px 31px 31px;
        height: 360px;
    }

    .tab-item__list-slide-title {
        time {
            line-height: 19px;
        }

        h4 {
            @include title-tablet-h2;

            margin: 11px 0 0;
        }
    }

    .tab-item__list-slide-photos-item {
        margin: 0 12px 0 0;
    }

    .tab-item__list-prev,
    .tab-item__list-next {
        width: 48px;
        height: 48px;
        background-repeat: no-repeat;
        background-size: 48px 48px;
        cursor: pointer;
        outline: none;

        &--article {
            width: 16px;
            height: 16px;
            background-repeat: no-repeat;
            background-size: 16px 16px;
        }
    }

    .tab-item__list-prev {
        &--article {
            background-image: url("../img/left-big.svg");
            margin: 4px 0 0;
            background-size: 10px 10px;

            &:hover {
                background-image: url("../img/left-big-hover.svg");
            }
        }
    }

    .tab-item__list-next {
        &--article {
            background-image: url("../img/right-big.svg");

            &:hover {
                background-image: url("../img/right-big-hover.svg");
            }
        }
    }

    .tab-item__slide-topic {
        margin: 24px 0 8px;
    }

    .tab-item__slide-description {
        font-size: 0.86em;
        line-height: 160%;
        max-height: 116px;
        margin: 0 0 18px;
    }

    .tab-item__slide-more {
        font-weight: bold;
        line-height: 120%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        text-decoration: none;
        color: $blue;

        &:hover {
            color: $hover-text;
        }
    }

    .tab-item__questionnaire-form {
        justify-content: flex-start;

        .tab-item__search-input {
            &--address-region {
                margin: 0 26px 0 0;
                width: 47.7%;
                max-width: 47.7%;

                select {
                    width: 100%;
                    max-width: 100%;
                }
            }
        }
    }

    .tab-item__questionnaire-button {
        width: 47.7%;
    }

    .tab-item__video-item {
        width: 47.5%;
        box-sizing: border-box;
        border: 1px solid $button-second;
        padding: 32px;
        margin: 0 24px 24px 0;

        &:last-child {
            margin: 0;
        }

        &:nth-child(2n) {
            margin: 0 0 24px;
        }

        iframe {
            width: 100%;
            height: 120px;
        }
    }

    .tab-item__picture {
        margin: 32px 0 0;

        img {
            display: block;
            margin: 0 auto;
            width: 100%;
            max-width: 640px;
            height: auto;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .tab-item {
        max-width: $mobile-max-width;
        padding: 0 24px;
        margin: 24px 0 0;

        &--address {
            padding: 0;
            text-align: center;

            h1 {
                padding: 0 24px;
                text-align: left;
                box-sizing: border-box;
            }

            h2 {
                padding: 0 24px;
                box-sizing: border-box;
            }
        }

        &--about-oms {
            & > h4 {
                margin: 24px 0 16px;
            }
        }

        &--health {
            width: 100%;
            max-width: $mobile-max-width;
            text-align: left;

            .tab-item__point-map {
                height: 330px;
            }

            .tab-item__view-section--list {
                .tab-item__point-list {
                    position: relative;
                    border: 1px solid $button-second;
                }

                .tab-item__point-item {
                    width: 100%;
                    max-width: 1212px;
                    border: none;
                    border-bottom: 1px solid $button-second;

                    &:not(:first-child) {
                        border-top: none;
                        border-bottom: 1px solid $button-second;
                        display: block;
                    }
                }
            }

            .tab__info-list {
                padding: 0 0 0 24px;
            }
        }
    }

    .tab-item__emphasis {
        padding: 24px 24px 14px 24px;
    }

    .tab-item__about-list {
        &--simple {
            & > li {
                ol {
                    padding: 0 0 0 12px;
                }
            }
        }
    }

    .tab-item__list-container {
        &--health {
            max-height: 3640px;
            min-width: unset;
            width: 100%;
            max-width: 591px;

            .tab-item__list-wrapper {
                flex-flow: column nowrap;
                height: max-content;
                min-width: unset;
                width: 100%;
                max-width: 591px;
            }

            .tab-item__list-slide {
                min-width: unset;
                width: 100%;
                max-width: 591px;
                max-height: 441px;
                box-sizing: border-box;
                padding: 32px 32px 38px;
                margin: 0 0 24px 0;

                &:nth-child(2n) {
                    margin: 0 0 24px 0;
                }
            }

            .tab-item__list-slide-title {
                h4 {
                    @include title-tablet-h2;

                    line-height: 22px;
                    margin: 0 0 16px;
                }
            }
        }
    }

    .tab-item__list-wrapper {
        max-width: 589px;

        &--interview-result {
            flex-direction: column;
        }
    }

    .tab-item__list-slide {
        width: 100%;
        max-width: 589px;

        &--result {
            max-width: 100%;
            flex-basis: 100%;

            h5 {
                @include title-h5;
            }
        }
    }

    .tab-item__list-slide-photos-list {
        width: 216px;
    }

    .tab-item__list-navigation {
        margin: 32px auto 0;
        width: 272px;
    }

    .tab-item__list-pagination {
        max-width: 160px;
        padding: 0;
    }

    .tab-item__list-pagination-bullet {
        margin: 0 8px 0 0;

        &:nth-child(3n) {
            margin: 0;
        }

        &:nth-child(n + 4) {
            display: none;
        }
    }

    .tab-item__questionnaire-form {
        display: block;

        .tab-item__search-input {
            &--address-region {
                margin: 0;
                width: 100%;
                max-width: 100%;

                select {
                    width: 100%;
                    max-width: 100%;
                }
            }
        }
    }

    .tab-item__questionnaire-button {
        width: 100%;
        margin: 16px 0 0;
    }

    .tab-item__video-list {
        @include list;

        display: block;
    }

    .tab-item__video-item {
        width: 100%;
        box-sizing: border-box;
        border: 1px solid $button-second;
        padding: 32px;
        margin: 0 0 16px 0;

        &:last-child {
            margin: 0;
        }

        &:nth-child(2n) {
            margin: 0 0 16px 0;
        }
    }

    .tab-item__video-item-title {
        margin: 24px 0 0;

        h2 {
            @include title-tablet-h2;

            margin: 0 0 8px;
        }

        time {
            font-size: 0.86em;
            line-height: 117%;
            letter-spacing: 0.02em;
            color: $light-gray;
        }
    }

    .tab-item__about-prevention {
        margin: 40px 0 0;
    }

    .tab-item__about-prevention-more {
        padding: 24px;
    }

    .tab-item__about-prevention-more-wrapper {
        display: block;

        p {
            font-family: $Stratos;
            font-size: 1.42em;
            line-height: 150%;
            margin: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;

            &::before {
                display: none;
            }
        }
    }

    .tab-item__about-prevention-more-button {
        width: 100%;
        margin: 16px 0 0 0;
    }
}
