.back-button {
    margin: 0 0 24px;

    a {
        font-family: $Stratos;
        font-size: 1.25rem;
        line-height: 120%;
        text-decoration: none;
        color: $blue;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &::before {
            display: block;
            content: "";
            background-image: url("../img/left-big.svg");
            background-repeat: no-repeat;
            background-position: center;
            width: 40px;
            height: 40px;
            background-color: $background-footer;
            border-radius: 4px;
            margin: 0 16px 0 0;
        }

        &:hover {
            color: $hover-text;

            &::before {
                background-image: url("../img/left-big-hover.svg");
            }
        }
    }
}

@media (max-width: $tablet-max-width) {
    .back-button {
        a {
            font-size: 1.25rem;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .back-button {
        padding: 0 0 0 24px;
    }
}
