.print-button {
    width: 188px;
    border: none;
    color: $white;
    background-color: $blue;
    font-weight: bold;
    font-size: 14px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    padding: 20px 0;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &::before {
        display: block;
        content: "";
        width: 16px;
        height: 16px;
        background-image: url("../img/print-icon.svg");
        margin: 0 12px 0 0;
    }

    &:hover {
        background-color: $hover-text;
    }

    &--all {
        width: 272px;
    }
}

@media (max-width: $tablet-max-width) {
    .print-button {
        display: none;
    }
}
