.tab__info {
    width: 903px;
    text-align: left;

    h1 {
        @include title-h1;
    }

    & > h3 {
        @include title-h3;

        color: $blue;
    }

    &--rights {
        width: 697px;

        .tab__info-document-list-wrapper {
            padding: 0 16px 0 52px;

            ol {
                padding: 0 0 0 16px;
            }
        }
    }
}

.tab__info-list {
    list-style: none;
    margin: 36px 0;
    padding: 0;

    &--point {
        margin: 0 0 30px;

        .tab__info-item-title {
            line-height: 120%;

            span {
                width: fit-content;
            }

            &::before {
                width: 32px;
                height: 32px;
                background-size: 32px 32px;
            }
        }

        .tab__info-item {
            & input:checked + .tab__info-item-title::before {
                width: 32px;
                height: 32px;
                background-size: 32px 32px;
            }
        }

        .tab__info-description {
            font-size: 0.75em;
            line-height: 120%;
            letter-spacing: 0.02em;
            width: 264px;
            margin: 0 0 0 48px;
        }

        .tab-item__point-holidays {
            color: $coral;
        }

        .tab-item__point-services {
            @include list;
            display: block;
        }

        .tab-item__point-service {
            font-size: 0.875em;
            line-height: 140%;
            width: 263px;
            margin: 0 0 16px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            background-image: none;

            &::before {
                display: block;
                content: "";
                min-width: 32px;
                height: 32px;
                background-repeat: no-repeat;
                background-size: cover;
                margin: 0 10px 0 0;
            }

            &--epoms {
                &::before {
                    background-image: url("../img/service-oms.svg");
                }
            }

            &--card {
                &::before {
                    background-image: url("../img/service-registration.svg");
                }
            }

            &--full_day {
                &::before {
                    background-image: url("../img/service-long.svg");
                }
            }

            &--weekend {
                &::before {
                    background-image: url("../img/service-weekend.svg");
                }
            }

            &--save_policy {
                &::before {
                    background-image: url("../img/service-e-policy.svg");
                }
            }

            &--policy {
                &::before {
                    background-image: url("../img/service-policy.svg");
                }
            }

            &--priem {
                &::before {
                    background-image: url("../img/service-manager.svg");
                }
            }

            &--ecard {
                &::before {
                    background-image: url("../img/service-card.svg");
                }
            }

            &--invalid {
                &::before {
                    background-image: url("../img/service-disabled.svg");
                }
            }
        }

        .tab-item__point-contacts {
            @include list;

            font-size: 1em;
            line-height: 120%;
            letter-spacing: 0.02em;

            a {
                text-decoration: none;
                color: $blue;

                &:hover {
                    color: $hover-text;
                }
            }

            li {
                margin: 0 0 8px 0;
            }
        }
    }

    &--filial-region {
        width: 904px;

        .tab__info-description {
            margin: 0 0 0 64px;
        }

        .tab__info-item-title {
            &::after {
                display: block;
                content: "";
                width: 93%;
                height: 1px;
                background-color: $button-second;
                position: absolute;
                bottom: -2px;
                left: 64px;
            }
        }

        .tab__info-item {
            & input:checked + .tab__info-item-title {
                &::after {
                    display: none;
                }
            }
        }

        .tab__info-document-list-wrapper {
            margin: 24px 0 0 24px;
        }
    }
}

.tab__info-item {
    width: 100%;
    margin: 0 0 5px;

    & input:checked + .tab__info-item-title {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        box-sizing: border-box;
    }

    & input:checked:hover {
        & + .tab__info-item-title {
            color: $hover-text;

            &::before {
                background-image: url("../img/minus-hover-icon.svg");
            }
        }
    }

    & input:checked + .tab__info-item-title::before {
        display: block;
        content: "";
        width: 48px;
        height: 48px;
        background-image: url("../img/minus-icon.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }

    & input:checked ~ .tab__info-description {
        display: block;
    }
}

.tab__info-item-title {
    font-family: $Stratos;
    font-weight: normal;
    font-size: 1.25em;
    line-height: 150%;
    color: $blue;
    cursor: pointer;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    padding: 5px 0;
    position: relative;
    z-index: 10;

    span {
        width: 93%;
    }

    &::before {
        display: block;
        content: "";
        width: 48px;
        height: 48px;
        margin: 0 16px 0 0;
        background-image: url("../img/plus-icon.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }

    &:hover,
    &:focus {
        color: $hover-text;

        &::before {
            background-image: url("../img/plus-hover-icon.svg");
        }
    }
}

.tab__info-description {
    display: none;

    &--policy {
        width: 838px;
        margin: 0 0 0 auto;

        a {
            text-decoration: none;
            color: $blue;

            &:hover {
                color: $hover-text;
            }
        }

        .tab__report-list {
            margin: 24px 0;
        }

        .tab__info-document-list-wrapper {
            padding: 0 16px 0 52px;

            ol {
                padding: 0 0 0 16px;
            }
        }
    }

    &--shedule {
        font-size: 0.75em;
        line-height: 120%;
        letter-spacing: 0.02em;
        width: 264px;
        margin: 0 0 0 auto;
    }

    &--rights {
        width: 77%;
        margin: 0 0 0 64px;

        a {
            text-decoration: none;
            color: $blue;

            &:hover {
                color: $hover-text;
            }
        }
    }

    &--health {
        width: 732px;
        margin: 0 0 24px auto;

        h3 {
            margin: 8px 0 16px;
        }

        h4 {
            @include title-h4;
            color: $blue;
            margin: 32px 0 0;
        }

        .tab__report-list {
            width: 100%;
            max-width: 735px;
        }
    }
}

.tab__info-description-link {
    @include title-h4;

    color: $blue;
    text-decoration: none;
    display: block;
    margin: 20px 0;

    &:hover {
        color: $hover-text;
    }
}

.tab__info-description-list,
.tab__info-requisites-list {
    @include list;

    position: relative;
    margin: 16px 0 0 0;
    width: 838px;

    &--policy {
        margin: 16px 0 0 0;
    }
}

.tab__info-description-list {
    &--policy {
        .tab__info-document-list {
            border: none;
        }

        .tab__info-document-item {
            &:not(:last-child) {
                border-bottom: none;
                margin: 0 0 8px;
            }

            display: inline-block;

            &::before {
                display: inline-block;
                content: "";
                margin: 0 5px 0 0;
            }
        }
    }

    &--health {
        width: 732px;
    }
}

.tab__info-description-item {
    box-sizing: border-box;
    padding: 20px 13px 20px 13px;
    margin: 0 0 18px 0;
    cursor: pointer;
    background-color: $background-footer;
    border-radius: 8px;

    &:hover,
    &:focus {
        .tab__info-description-item-title {
            color: $hover-text;

            &::before {
                background-image: url("../img/arrow_down-hover-icon.svg");
            }
        }
    }

    & input:checked + .tab__info-description-item-title::before {
        transform: rotate(180deg);
        transition: all 0.5s;
    }

    & input:checked ~ .tab__info-document-list-wrapper--hide {
        display: block;
    }

    .tab__report-list {
        width: 100%;
        max-width: 838px;
    }

    .tab__report-item-link {
        padding: 24px 40px 20px 12px;
    }

    .tab__info-document-list {
        width: 100%;
    }
}

.tab__info-description-item-title {
    @include title-h5;

    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;

    &::before {
        display: block;
        content: "";
        min-width: 40px;
        height: 40px;
        border-radius: 50%;
        margin: 0 16px 0 0;
        background-image: url("../img/arrow_down-icon.svg");
        background-repeat: no-repeat;
        background-size: 24px 24px;
        background-color: $white;
        background-position: center;
        transition: all 0.5s;
    }
}

.tab__info-document-list-wrapper {
    &--hide {
        display: none;
    }

    & > h5 {
        display: none;
    }

    & > ol,
    & > ul {
        margin: 16px 0 0;
    }

    .tab__contacts-list--filial {
        padding: 0;
        background-color: unset;
    }

    &--health {
        padding: 0 55px;

        ol {
            margin: 0;
            padding: 0;
        }
    }
}

.tab__info-document-list-wrapper-toggle {
    display: none;
}

.tab__info-document-list {
    @include list;

    border: 1px solid $button-second;
    margin: 24px 0 0 56px;
    width: 670px;
}

.tab__info-document-item {
    &:not(:last-child) {
        border-bottom: 1px solid $button-second;
    }

    &--policy {
        &:not(:last-child) {
            border-bottom: none;
        }

        display: flex;
        justify-content: flex-start;

        &::before {
            display: block;
            content: "";
            margin: 0 5px 0 0;
        }
    }
}

.tab__info-document-item-link {
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 23px 13px 19px;
    position: relative;

    &:hover {
        background-color: $background-footer;

        &::before {
            background-image: url("../img/rule_doc-hover.svg");
        }

        .tab__info-document-item-description {
            h5 {
                color: $hover-text;
            }

            p {
                color: $hover-text;
            }
        }

        &::after {
            display: block;
            content: "";
            width: 16px;
            height: 20px;
            background-image: url("../img/right-big-hover.svg");
            background-repeat: no-repeat;
            position: absolute;
            right: 27px;
            top: calc(50% - 8px);
        }
    }

    &::before {
        display: block;
        content: "";
        min-width: 40px;
        height: 40px;
        margin: 0 16px 0 0;
        background-image: url("../img/rule_doc.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }
}

.tab__info-document-item-description {
    h5 {
        @include title-h5;
    }

    p {
        font-size: 0.75em;
        line-height: 120%;
        letter-spacing: 0.02em;
        margin: 0;
        color: $black-text;
    }
}

.tab__info-requisites-item {
    display: flex;
    justify-content: flex-start;
    margin: 8px 0 0;
}

.tab__info-requisites-item-name {
    color: $light-gray;
    width: 190px;
    margin: 0 62px 0 0;
}

.tab__info-requisites-item-value {
    width: 180px;
    margin: 0;
}

.tab__report-list {
    @include list;

    width: 838px;
    border: 1px solid $button-second;
    margin: 16px 0 0 auto;
}

.tab__report-item {
    &:not(:last-child) {
        border-bottom: 1px solid $button-second;
    }
}

.tab__report-item-link {
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 23px 13px 19px;
    position: relative;

    &:hover {
        background-color: $background-footer;

        &::before {
            background-image: url("../img/rule_doc-hover.svg");
        }

        .tab__rule-item-description {
            h5 {
                color: $hover-text;
            }

            p {
                color: $hover-text;
            }
        }

        .tab__report-item-description {
            h5 {
                color: $hover-text;
            }

            p {
                color: $hover-text;
            }
        }

        &::after {
            display: block;
            content: "";
            width: 16px;
            height: 20px;
            background-image: url("../img/right-big-hover.svg");
            background-repeat: no-repeat;
            position: absolute;
            right: 27px;
            top: calc(50% - 8px);
        }
    }

    &::before {
        display: block;
        content: "";
        min-width: 40px;
        height: 40px;
        margin: 0 16px 0 0;
        background-image: url("../img/rule_doc.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }
}

.tab__report-item-description {
    h5 {
        @include title-h5;
    }

    p {
        font-size: 0.75em;
        line-height: 120%;
        letter-spacing: 0.02em;
        color: $black-text;
        margin: 0;
    }
}

.tab__description-list {
    list-style: none;
    padding: 0 0 0 16px;
    margin: 24px 0;

    li {
        display: flex;
        justify-content: flex-start;
        margin: 0 0 16px;

        &::before {
            display: block;
            content: "-";
            margin: 0 5px 0 0;
        }
    }

    &--health {
        padding: 0 0 0 20px;

        li {
            display: block;
            position: relative;

            &::before {
                content: "";
                min-width: 8px;
                height: 8px;
                border-radius: 50%;
                background-color: $blue;
                margin: 10px 12px 0 0;
                position: absolute;
                left: -20px;
            }
        }
    }
}

@media (max-width: $tablet-max-width) {
    .tab__info {
        width: 100%;
        max-width: $tablet-max-width;
        box-sizing: border-box;
        padding: 0 32px;
        margin: 0 auto;

        h1 {
            @include title-tablet-h1;

            margin: 40px 0 0;
        }

        & > h3 {
            @include title-tablet-h2;

            color: $blue;
        }
    }

    .tab__info-list {
        margin: 16px 0;

        &--filial-region {
            box-sizing: border-box;
            width: 100%;
            max-width: $tablet-max-width;

            .tab__info-description {
                margin: 0 0 0 40px;
                width: unset;
            }

            .tab__info-item-title {
                &::after {
                    width: 93%;
                    bottom: 0;
                    left: 48px;
                }
            }
        }
    }

    .tab__info-item {
        margin: 0;

        & input:checked {
            & + .tab__info-item-title {
                &::after {
                    display: none;
                }
            }
        }

        & input:checked:hover {
            & + .tab__info-item-title {
                &::before {
                    background-image: url("../img/minus-hover-icon-tablet.svg");
                }
            }
        }

        & input:checked + .tab__info-item-title::before {
            width: 32px;
            height: 32px;
            background-image: url("../img/minus-icon-tablet.svg");
        }
    }

    .tab__info-item-title {
        font-family: "Arial", "Helvetica", sans-serif;
        font-size: 1.14em;
        line-height: 120%;
        padding: 16px 0;
        position: relative;

        &::after {
            display: block;
            content: "";
            width: 83vw;
            height: 1px;
            background-color: $button-second;
            position: absolute;
            bottom: 0;
            left: 46px;
        }

        span {
            width: 80%;
            max-width: 800px;
        }

        &::before {
            width: 32px;
            height: 32px;
            background-image: url("../img/plus-icon-tablet.svg");
        }

        &:hover,
        &:focus {
            &::before {
                background-image: url("../img/plus-hover-icon-tablet.svg");
            }
        }
    }

    .tab__info-description {
        &--policy {
            width: 92%;
            margin: 0 0 0 49px;
        }

        &--health {
            width: 92%;
            margin: 0 0 0 49px;
        }
    }

    .tab__info-description-list,
    .tab__info-requisites-list {
        position: relative;
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        width: 98%;
    }

    .tab__info-description-list {
        margin: -8px 0 0;

        &--policy {
            padding: 0;
        }
    }

    .tab__info-description-item {
        box-sizing: border-box;
        padding: 20px 13px 19px 0;
        margin: 0;
        cursor: pointer;
        background-color: unset;
        border-radius: unset;
        border-bottom: 1px solid $button-second;

        &:hover,
        &:focus {
            .tab__info-description-item-title {
                &::before {
                    background-image: unset;
                }

                &::after {
                    background-image: url("../img/right-tablet-hover.svg");
                }
            }
        }

        & input:checked ~ .tab__info-document-list-wrapper--hide {
            display: none;
        }
    }

    .tab__info-description-item-title {
        font-family: "Arial", "Helvetica", sans-serif;
        font-size: 0.857em;
        line-height: 130%;
        color: $blue;

        &::before {
            display: none;
        }

        &::after {
            display: block;
            content: "";
            width: 8px;
            height: 12px;
            margin: 0;
            background-image: url("../img/right-tablet.svg");
            background-size: cover;
            position: absolute;
            right: 0;
        }
    }

    .tab__info-document-list-wrapper {
        box-sizing: border-box;
        padding: 32px;
        background-color: $white;
        position: fixed;
        overflow-y: auto;
        z-index: 100;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;

        & > h5 {
            @include title-tablet-h1;

            display: block;
            margin: 0 0 23px;
        }

        &--filial-region {
            & > h5 {
                @include title-tablet-h1;

                color: $blue;
            }
        }
    }

    .tab__info-document-list-wrapper-toggle {
        display: block;
        width: 32px;
        height: 32px;
        background-image: url("../img/menu-close-icon.svg");
        background-color: transparent;
        border: none;
        margin: 0 0 40px auto;
        cursor: pointer;
        outline: none;

        &:hover {
            background-image: url("../img/menu-hover-close-icon.svg");
            outline: none;
        }
    }

    .tab__info-document-list {
        margin: 0;
        width: 100%;
    }

    .tab__info-document-item-link {
        padding: 23px 45px 19px 13px;
    }

    .tab__info-requisites-item {
        display: block;
        justify-content: flex-start;
        margin: 8px 0 0;

        p:first-of-type {
            font-size: 0.857em;
            line-height: 160%;
        }

        p:nth-of-type(2) {
            line-height: 120%;
        }
    }

    .tab__report-list {
        @include list;

        width: 92%;
        margin: 16px 0 0 0;
    }

    .tab__report-item-link {
        padding: 23px 35px 19px 13px;
    }

    .tab__info-description-link {
        @include title-tablet-h2;

        color: $blue;
        margin: 20px 0;
    }
}

@media (max-width: $mobile-max-width) {
    .tab__info {
        max-width: $mobile-max-width;
        padding: 0 24px;

        h1 {
            margin: 24px 0 0;
        }
    }

    .tab__info-list {
        &--point {
            margin: 0 0 20px;

            .tab__info-description {
                width: 200px;
            }

            .tab-item__point-service {
                font-size: 0.857em;
                line-height: 160%;
                width: 220px;
                margin: 0 0 8px;
            }

            .tab-item__point-contacts {
                @include list;

                font-size: 1em;
                line-height: 120%;
                letter-spacing: 0.02em;

                a {
                    text-decoration: none;
                    color: $blue;

                    &:hover {
                        color: $hover-text;
                    }
                }

                li {
                    margin: 0 0 8px 0;
                }
            }
        }

        &--filial-region {
            padding: 0 24px;
            text-align: left;
        }
    }

    .tab__info-item-title {
        &::after {
            width: 70vw;
        }
    }

    .tab__info-description {
        &--policy {
            width: 85%;
        }

        &--health {
            width: 85%;
        }
    }

    .tab__info-description-item {
        padding: 20px 20px 19px 0;
    }

    .tab__info-document-list-wrapper-toggle {
        width: 32px;
        height: 32px;
    }

    .tab__info-document-item-link {
        align-items: flex-start;
    }

    .tab__report-item-link {
        align-items: flex-start;
    }
}
