.description-help {
    display: inline-block;
    width: 14px;
    height: 14px;
    background-image: url("../img/info.svg");
    position: relative;
    z-index: 10;

    &__data {
        visibility: hidden;
        display: block;
        box-sizing: border-box;
        padding: 8px 10px;
        background-color: $black-background;
        color: $white;
        width: max-content;
        max-width: 370px;
        font-size: 12px;
        line-height: 150%;
        letter-spacing: 0.02em;
        border-radius: 8px;
        position: absolute;
        bottom: 32px;
        opacity: 0.5;
        transition: 0.2s all;
    }

    &::after {
        visibility: hidden;
        position: absolute;
        bottom: 12px;
        right: 0;
        display: block;
        content: "";
        border: 8px solid transparent;
        border-top: 12px solid $black-background;
        opacity: 0.5;
        transition: 0.2s all;
    }

    &:hover {
        .description-help__data {
            visibility: visible;
            opacity: 1;
            transition: 0.4s opacity;
        }

        &::after {
            visibility: visible;
            opacity: 1;
            transition: 0.4s opacity;
        }
    }

    &--left {
        .description-help__data {
            right: -28px;
        }
    }

    &--right {
        .description-help__data {
            left: -32px;
        }
    }
}
